.zion-header{
  &-section {
    transition: all 0.4s;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: auto;
    z-index: 10;
    text-align: center;
    background-color: var(--white-color);
    @include max (991px){
      padding: 20px 0;
    }
  }
}
header.hide-header {
    transform: translateY(-100%);
}
.zion-main-menu-item{
  text-align: center;
}
.zion-header-section.sticky-menu{
  box-shadow: 0 .125rem .5rem rgba(0,0,0,.04);
  background-color: var(--white-bg);
}

.zion-header{
  &-info-data{
    ul{
      li{
          display: inline-block;
          margin-right: 20px;
          font-size: 16px;
          line-height: 24px;
          font-weight: 600;
          color: var(--white-color);
          position: relative;
          padding-left: 30px;
          &:last-child{
            margin-right: 0;
          }
          img{
            position: absolute;
            top: 2px;
            left: 0;
            
          }
          a{
            color: var(--white-color);
            img{
              position: absolute;
              top: 5px;
              left: 0;
            }
          }
      }
    }
  }
  
}



.header-logo1 {
  a{
    padding: 30px 0px;
  }
}

// end main


.zion-header-main-menu{
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 20px;
  padding-left: 40px;
  padding-right: 20px;
  @include max (1350px){
    padding-left: 24px;
  }
  @include max (1199px){
    justify-content: end;
    background: none;
    padding-right: 0;
  }
  &.menu2{
    transition: all 0.4s;
    padding: 0;
     
  }
}

// End v1



.dark-color{
  &.site-menu-main .nav-link-item{
    color: var(--white-color);
  }
}

.site-header .zion-menu-toggle.dark-color {
  span{
    background-color: var(--heading-color);
    &::before, 
    &::after{
      background-color: var(--heading-color);
    }
  }
}
.site-header .zion-menu-toggle.white-color {
  span{
    background-color: var(--white-color);
    &::before, 
    &::after{
      background-color: var(--white-color);
    }
  }
}

// End v2
  .header-bottom-border{
    border-bottom: 1px solid #272F5D;
    &.border-color-light{
      border-bottom: 1px solid rgba(255, 255, 255, 0.12);
    }
    @include max(991px) {
      padding: 20px 0;
    }
  }


.zion-header{
  &-triger {
      width: 60px;
      height: 60px;
      display: flex;
      cursor: pointer;
      justify-content: center;
      align-items: center;
      background: var(--accent-bg);
      border-radius: 100px;
      margin-left: 20px;
    &.triger2 {
