}


.zion-header-icon {
  display: flex;
  align-items: center;
  gap: 20px;
  position: relative;
  margin: 0 30px;
  @include max(991px){
    display: none;
  }
 
}

a.zion-header-info{
  font-size: 20px;
  font-weight: 700;
  line-height: 26px;
  color: var(--dark-bg);
  padding: 12px 0;
  img{
    padding-right: 10px;
  }
}

.zion-contact-info{
  margin-bottom: 30px;
  i{
    display: block;
    font-size: 24px;
    margin-bottom: 15px;
    line-height: 24px;
  }
  h5{
    margin-bottom: 5px;
  }
  img{
    width: 5%;
    margin-bottom: 15px;
  }
