    color: var(--body-color);
  }
} 


// start sidebar menu

.zion-sidemenu-wraper{
  position: relative;
}

.zion-sidemenu-column {
    width: 400px;
    display: block;
    height: 100%;
    position: fixed;
    z-index: 111;
    padding: 30px;
    margin-right: -440px;
    transition: 0.5s;
    right: 0;
    top: 0;
    padding-top: 70px;
    overflow-y: scroll;
    background-color: var(--white-bg);
 
}
.zion-sidemenu-thumb{
  margin: 30px 0;
  img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius:5px;
  }
}

.zion-sidemenu-column.active {
  margin-right: 0px;
  z-index: 9999;
}

.zion-sidemenu-close {
  position: absolute;
  left: 0;
  top: 0;
  cursor: pointer;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--accent-bg);
  i{
    font-size: 30px;
    color: var(--white-color);
    transition: all 0.4s;
  }

}
.active .zion-sidemenu-close{
  opacity: 1;
  visibility: visible;
}


.offcanvas-overlay {
      top: 0;
      width: 100%;
      height: 100%;
      background: #000;
      position: fixed;
      z-index: 0;
      visibility: hidden;
      opacity: 0;
      transition: .5s;
 }

.offcanvas-overlay.active {
      opacity: .7;
      z-index: 111;
      visibility: visible;
 }

.zion-sidemenu-logo{
  margin-bottom: 30px;
  img{
    width: 70%;
  }
}



.zion-sidemenu-column::-webkit-scrollbar-track{
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
  background-color: #F5F5F5;
}

.zion-sidemenu-column::-webkit-scrollbar{
  width: 5px;
  background-color: #F5F5F5;
}

.zion-sidemenu-column::-webkit-scrollbar-thumb{
  background-color: #000000;
  border: 2px solid #555555;
}


@media (max-width: 767px) {
.zion-sidemenu-wraper{
  display: none;
}

}
// End sidebar







.main-menu {
  a {
    color: var(--heading-color);
    font-weight: 400;
    position: relative;
    display: block;
    transition: all 0.4s;
    text-transform: capitalize;
  }
  &.dark-color{
    a{
      color: var(--heading-color);
    }
  }

  >ul {
    >li {
      margin: 0 17px;
      @include max (1199px){
        margin: 0 10px;
      }
    }
  }


  ul {
    margin: 0;
    padding: 0;

    li {
      list-style-type: none;
      display: inline-block;
      position: relative;
      &.menu-item-has-children {
        >a {
          &:after {
            content: "\ea4d";
            position: relative;
            margin-left: 2px;
            top: 2px;
            font-size: 20px;
            display: inline-block;
            font-family: 'remixicon';
            transition: all 0.4s ease-in-out;
          }
        }
      }
      &:hover{
        &.menu-item-has-children {
          >a {
            &:after {
              transform: rotate(-180deg);
            }
            
          }
        }
      }
      &:last-child {
        margin-right: 0;
      }

      &:first-child {
        margin-left: 0;
      }

      &:hover {

        >ul.sub-menu {
          visibility: visible;
          opacity: 1;
          z-index: 99;
          pointer-events: visible;
          transform: translateY(0);
        }
      }

    }
  }

  
  ul.sub-menu {
    z-index: 500;
    background-color: #fff;
    box-shadow: -2px 2px 70px -25px rgb(0 0 0 / 30%);
    position: absolute;
    left: 0;
    top: 90px;
    margin: 0;
    padding: 12px 0;
    transition: .4s;
    z-index: 4;
    transform: translatey(20px);
    opacity: 0;
    text-align: left;
    visibility: hidden;
    min-width: 220px;
    border-radius: 6px;
    
    li {
      padding: 5px 10px 5px 20px;
      a {
        font-size: 18px;
        line-height: 30px;
        display: inline-block;
        position: relative;
        font-weight: 400;
        text-transform: capitalize;
        
      }
      &.menu-item-has-children  {
        a.no-border{
          display: block;
          &::after{
            position: absolute;
            right: 0;
          }
          &::before{
            content: none;
          }
        }
      }
      ul.sub-menu {
        transition: all 0.4s;
        margin-left: 0px!important;
        transform: translateY(30px);
      }
      &:hover ul.sub-menu{
        transform: translateY(0px);
      }
    }
    
  }




  ul.sub-menu {
    padding: 12px 0;
    left: -27px;
    top: 86px;

    
    li {
      display: block;
      margin: 0 0;
      transition: all 0.4s;

      ul.sub-menu {
        left: 100%;
        right: auto;
        top: 0;
        margin: 0 0;
        margin-left: 20px;
        border-radius: 10px;
        li {
          ul {
            left: 100%;
            right: auto;
          }
        }
      }
    }
  }
}


ul.sub-menu{
  li{
    a{
      color: var(--heading-color)!important;
      &::before{
        content: '';
        position: absolute;
        bottom: -3px;
        left: auto;
        right: 0;
        height: 2px;
        width: 0;
        background-color: var(--heading-color)!important;
        transition: all 0.3s ease;
        @include max (767px){
          height: 0;
        }
      }
      &:hover::before{
        left: 0;
        right: auto;
        width: 100%;
      }
    }
  }
}
a.light-color{
  color: var(--light-color);
}

.menu-style1 {
