
.zion-default-btn{
  padding: $button_padding;
  font-weight: $button_font_weight;
  font-family: $button_font_family;
  border-radius: $buton_border_radius;
  border: 1px solid var(--heading-color);
  display: inline-flex;
  align-items: center;
  gap: 12px;
  font-size: 18px;
  line-height: 1.44em;
  color: var(--heading-color)!important;
  transition: all 0.4s ease;
  width: max-content;
  position: relative;
  span{
    position: relative;
    overflow: hidden;
    display: block;
    width: 14px;
    height: 14px;
    .arry1 {
      position: absolute;
      top: 0px;
      left: 0px;
      transition: 0.3s cubic-bezier(0.37, 0.08, 0.02, 0.93);
      color: var(--heading-color);
    }
    .arry2 {
      position: absolute;
      bottom: -15px;
      left: -17px;
      transition: 0.3s cubic-bezier(0.37, 0.08, 0.02, 0.93);
      color: var(--heading-color);
    }
  }
  @include max (767px){
    padding: 18px 30px;
  }
  &:hover {
    background-color: var(--accent-bg);
    border: 1px solid transparent;
    span {
      .arry1 {
        top: -15px;
        left: 15px;
        color: var(--heading-color);
      }
      .arry2 {
        bottom: 0;
        left: 0;
        color: var(--heading-color);
      }
    }
  }
  &.zion-header-btn{
    border: none;
    padding: 0;
    border-radius: 0;
    background-color: #fff!important;
    &:hover{
      background-color: none!important;
    }
    @include max (767px){
      display: none;
    }
    &::before{
      content: "";
      position: absolute;
      width: 90px;
      height: 2px;
      bottom: 0;
      left: 0;
      background-color: var(--heading-color);
    }
  }
  &.d-block {
    width: 100%;
  }
  &:focus {
    box-shadow: none;
  }
}
.zion-title-btn {
  @include max (991px){
    width: 100%;
    text-align: center;
    margin-top: 30px;
  }
}



// add portfolio btn

.zion-portfolio-btn {
  white-space: nowrap;
  text-overflow: ellipsis;
  -o-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
  position: relative;
  overflow: hidden;
  transform-style: preserve-3d;
  width: 60px;
  height: 60px;
  display: flex;
  border: 1px solid #000;
  align-items: center;
  justify-content: center;
  border-radius: 100px;
  @include max (767px){
    width: 50px;
  }
  i{
    font-size: 28px;
    color: var(--heading-color);
    transition: all 0.4s ease-in-out;
  }
  &:hover {
    color: var(--heading-color);
   
  }
  &::before{
    background-repeat: no-repeat;
    position: absolute;
    content: "\ea70";
    right: -8px;
    top: 0;
    opacity: 1;
    height: 100%;
    font-size: 28px;
    color: var(--heading-color);
    font-family: remixicon !important;
    transform: translate(-65px,65px);
    transition: all 0.2s linear 0s;
  }
  @include max (767px){
    padding: 18px 30px;
  }
}
.zion-service-content-icon{
  display: inline-flex;
  align-items: center;
  transition: all 0.4s ease;
  width: max-content;
  position: relative;
  span{
    position: relative;
    overflow: hidden;
    display: block;
    width: 14px;
    height: 14px;
    &.icon2{
      width: 30px;
      height: 30px;
    }
    .arry1 {
      position: absolute;
      top: 0px;
      left: 0px;
      transition: 0.3s cubic-bezier(0.37, 0.08, 0.02, 0.93);
      color: var(--heading-color);
    }
    .arry2 {
      position: absolute;
      bottom: -15px;
      left: -17px;
      transition: 0.3s cubic-bezier(0.37, 0.08, 0.02, 0.93);
      color: var(--heading-color);
      &.arry3{
        bottom: -30px;
        left: -30px;
      }
    }
  }
  @include max (767px){
    padding: 18px 30px;
  }
  &:hover {
    span {
      .arry1 {
        top: -15px;
        left: 15px;
      }
      .arry2 {
        bottom: 0;
        left: 0;
      }
    }
  }
}