.zion-hero{
  &-content{
    position: relative;
    padding: 210px 0 0;
    @include max (1199px){
      padding: 190px 0 0;
    }
    @include max (991px){
      padding: 170px 0 0;
    }
    @include max (767px){
      padding: 150px 0 0;
    }
  }
  &-thumb{
    padding: 80px 0 60px 0;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    img{
      border-radius: 10px;
    }
    @include max (991px){
    padding: 60 0 40px 0;
    grid-template-columns: repeat(2, 1fr);
    }
    @include max (767px){
      padding: 40px 0 30px 0;
      grid-template-columns: repeat(1, 1fr);
    }
  }
  // end one
  &-section2{
    position: relative;
    z-index: 0;
    margin-top: 80px;
    padding: 130px 0 100px;
    background-size: cover;
    background-repeat: no-repeat;
    &::before{
      content: "";
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      position: absolute;
      background: linear-gradient(180deg, rgba(29, 29, 27, 0) 0%, #1D1D1B 100%);
    }
  }
  &-content2{
    position: relative;
    max-width: 1000px;
    text-align: center;
    margin: 0 auto;
    h1{
      color: var(--white-color);
    }
  }
  &-data-wrap{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 130px;
    @include max (991px){
      padding-top: 100px;
    }
    @include max (767px){
      padding-top: 80px;
    }
    @include max (575px){
      padding-top: 60px;
    }
  }
  &-data-item{
    position: relative;
    max-width: 310px;
    &.item2{
      max-width: 335px;
      text-align: right;
    }
    h4{
      color: var(--white-color);
    }
  }
}
.zion-sub-title{
  padding: 10px 20px;
  border-radius: 50px;
  display: inline-block;
  margin-bottom: 24px;
  background-color: rgba(29, 29, 27, 0.05);
  p{
    font-weight: 700;
    font-size: 18px!important;
    line-height: 1.33em!important;
    text-transform: uppercase;
    @include max (767px){
      font-weight: 500;
    }
  }
  &.title2{
    background-color: rgba(255, 255, 255, 0.05);
    p{
      color: var(--white-color);
    }
  }
}

.zion-default-bg{
  margin: 0 30px 30px;
  border-radius: 10px;
  background-color: var(--light-bg);
  @include max (991px){
    margin: 0 20px 20px;
  }
  @include max (767px){
    margin: 0 10px 10px;
  }
}
.margin-30{
  margin: 30px;
}