.zion-blog{
    &-wrap{
        transition: all 0.4s;
        &:hover{
            .zion-blog-title h3{
                background-size: 100% 1px;
            }
        }
        @include max (1199px){
            padding-bottom: 50px;
        }
        @include max (991px){
            padding-bottom: 40px;
        }
        @include max (767px){
            padding-bottom: 30px;
        }
    }
    &-thumb{
        flex-shrink: 0;
        img{
            border-radius: 10px;
        }
    }
    &-meta{
        padding: 24px 0;
        ul{
            li{
                a{
                    color: var(--heading-color);
                }
            }
        }
    }
    &-title{
        h3{
            background-image: linear-gradient(currentColor, currentColor);
            background-position: 0 100%;
            background-repeat: no-repeat;
            background-size: 0 1px;
            transition: background-size .3s ease-in-out;
            display: inline;
        }
        @include max (1199px){
            max-width: 70%;
        }
        @include max (991px){
            max-width: 100%;
        }
    }
    &-wraper{
        @include max (1199px){
            display: flex;
            gap: 24px;
        }
         @include max (767px){
            display: block;
        }
    }
    &-wrap2{
        display: flex;
        align-items: center;
        gap: 24px;
        transition: all 0.4s;
        @include max (1199px){
            display: block;
        }
        &:hover{
            .zion-blog-thumb2 img{
                transform: scale(1.1);
            }
            .zion-blog-title2 h3{
                background-size: 100% 1px;
            }
        }
    }
    &-thumb2{
        flex-shrink: 0;
        position: relative;
        overflow: hidden;
        border-radius: 10px;
        img{
            object-fit: cover;
            transition: all 0.4s;
            border-radius: 10px;
            @include max (1199px){
                width: 100%;
                height: 414px;
            }
            @include max (991px){
                height: 300px;
            }
        }
    }
    &-title2{
        h3{
            background-image: linear-gradient(currentColor, currentColor);
            background-position: 0 100%;
            background-repeat: no-repeat;
            background-size: 0 1px;
            transition: background-size .3s ease-in-out;
            display: inline;
        }
        @include max (1199px){
            max-width: 70%;
        }
        @include max (991px){
            max-width: 100%;
        }
    }
}
.mb-24{
    margin-bottom: 24px;
}

.zion-title-btn {
    @include max (991px){
        width: 100%;
        text-align: center;
        margin-top: 30px;
    }
}