.zion-t{
    &-section{
        padding: 130px 0 210px;
    }
    &-wrap{
        padding: 30px;
        margin: 0 15px;
        border-radius: 10px;
        background-color: var(--white-bg);
    }
    &-author-wrap{
        display: flex;
        align-items: center;
        gap: 12px;
    }
    &-author-thumb{
        img{
            border-radius: 100px;
        }
    }
    &-author-data{
        h6{
            font-size: 20px;
            line-height: 1.33em;
            padding-bottom: 5px;
        }
        p{
            font-weight: 400;
            font-size: 18px;
            line-height: 1.44em;
        }
    }
}



.mCSB_dragger{
    width: 96px!important;
}
.mCSB_scrollTools.mCSB_scrollTools_horizontal {
    width: 636px!important;
    height: 6px;
    top: auto;
    right: 0;
    bottom: 0!important;
    left: 0;
    margin: 0 auto;
    @include max (991px){
        width: 400px!important;
    }
    @include max (767px){
        width: 300px!important;
    }
}
.mCSB_scrollTools .mCSB_draggerContainer{
    top: 80px;
}
