.zion-section-padding{
    padding: 130px 0;
    @include max (991px){
        padding: 80px 0;
    }
    @include max (767px){
        padding: 60px 0;
    }
}
.zion-section-padding2{
    padding: 130px 0 106px;
    @include max (991px){
        padding: 80px 0 56px;
    }
    @include max (767px){
        padding: 60px 0 36px;
    }
    &.section{
        border-radius: 20px;
        background-color: var(--accent-bg);
        h2{
            color: var(--white-color);
        }
    }
}
.zion-section-padding3{
    padding: 0 0 106px;
    @include max (991px){
        padding: 0 0 76px;
    }
    @include max (767px){
        padding: 0 0 36px;
    }
}


// section title

.zion-section-title{
    padding-bottom: 80px;
    @include max (991px){
        padding-bottom: 60px;
        text-align: center;
    }
    @include max (767px){
        padding-bottom: 40px;
    }
    &.max-680{
        max-width: 680px;
    }
    &.max-750{
        max-width: 750px;
    }
    &.title2{
        @include max (991px){
            text-align: left;
        }
    }
    &.center{
        max-width: 650px;
        margin: 0 auto;
        text-align: center;
        &.max-width-700{
            max-width: 700px;
        }
        &.max-width-750{
            max-width: 750px;
        }
        &.width-850{
            max-width: 850px;
            padding-bottom: 80px;
            @include max (991px){
                padding-bottom: 60px;
            }
            @include max (767px){
                padding-bottom: 40px;
            }
        }
    }
    
}
// default content
.zion-default-content{
    h2{
        padding-bottom: 20px;
    }
    p{
        font-size: 28px;
        line-height: 1.21em;
    }
    &.content2{
        h1,h2,h3,h4,p{
            color: var(--white-color);
        }
    }
}
.title{
    padding-bottom: 20px;
}
.mt-50{
    margin-top: 50px;
    @include max (991px){
        margin-top: 40px;
    }
    @include max (767px){
        margin-top: 30px;
    }
}
.section{
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

.pr-50{
    padding-right: 50px;
    @include max (991px){
        padding-right: 0;
    }
}
.pl-50{
    padding-left: 50px;
    @include max (991px){
        padding-left: 50px;
    }
}