.zion-cta{
    &-section{
        padding: 100px 0;
        @include max (991px){
            padding: 80px 0;
        }
        @include max (767px){
            padding: 60px 0;
        }
    }
    &-content{
        display: flex!important;
        align-items: center;
        gap: 100px;
        white-space: nowrap;
        h2{
            color: var(--white-color);
        }
    }
}

.zion-cta-slider-init2{
    margin-top: 15px;
    &.slick-initialized.slick-slider {
    direction: rtl;
}
}