// fonts variable
$body-font: "Satoshi",!default;
$heading-font: "Satoshi",!default;
                                                

// color variable
:root {
  --heading-color: #1D1D1B;
  --heading-bg: #1D1D1B;
  --white-color: #fff;
  --white-bg: #fff;
  --accent-color: rgba(157, 248, 86, 1);
  --accent-bg: rgba(157, 248, 86, 1);
  --light-bg: rgba(246, 246, 246, 1);
  --light-color1: rgba(255, 255, 255, 1);
}

$colors: (
 
);

$bodyfontScale: (
  xl: (
    20: 20px,
    lineheight: 32px,
  ),
  lg: (
    18: 18px,
    lineheight: 28px,
  ),
  md: (
    16: 16px,
    lineheight: 24px,
  ),
  xs: (
    14: 14px,
    lineheight: 22px,
  ),
  xss: (
    12: 12px,
    lineheight: 20px,
  ),
);

// button font size
$button_font_size: 16px;
$buton_line_height: 1.5em;
$buton_border_radius: 50px;
$button_padding: 17px 32px;
$button_font_weight: 700;
$button_font_family: $body-font;