input[type="text"],
input[type="email"],
input[type="password"],
input[type="url"],
input[type="tel"],
input[type="time"],
input[type="date"],
input[type="search"],
input, textarea {
  margin: 0;
  width: 100%;
  height: 60px;
  color:var(--heading-color);
  border-radius: 0;
  background-color: transparent;
  padding: 15px 0 ;
  border: none;
  transition: all 0.4s;
}
textarea {
  height: 145px;
  resize: none;
  &::placeholder{
   color: #181B0E99!important;
   }
  @include max(767px) {
    height: 100px;
  }
}
.zion-main{
   &-field{
      p{
         margin-bottom: 10px;
         font-size: 18px;
         font-weight: 700;
         line-height: 30px;
      }
      input{
        padding: 18px 24px;
        border-radius: 10px;
        font-size: 16px;
        line-height: 24px;
        background-color: var(--light-color2);
        &::placeholder{
         color: #181B0E99;
        }
      }
      &-textarea{
         background: var(--light-color2);
         padding: 24px;
         border-radius: 10px;
         margin-bottom: 24px;
         p{
            margin-bottom: 10px;
            font-size: 18px;
            font-weight: 700;
            line-height: 30px;
         }
         &.textarea2{
            background-color: var(--light-color);
         }
      }
      &.field2{
         input{
            background-color: var(--light-color);
         }
      }
   }
}

.input-form{
   input{
      height: 50px;
      padding: 20px;
      font-size: 16px;
      line-height: 24px;
      border-radius: 50px;
      margin-bottom: 15px;
      border: 1px solid var(--heading-color);
   }
   button{
      width: 100%;
      height: 50px;
      font-size: 16px;
      line-height: 24px;
      border-radius: 50px;
      transition: all 0.4s;
      color: var(--light-color);
      background-color: var(--accent-color);
      &:hover{
         background-color: var(--heading-color);
      }
   }
}

input::-webkit-input-placeholder, 
textarea::-webkit-input-placeholder{ /* WebKit, Blink, Edge */
    color:    var(--heading-color);
    opacity: 1;
}
input:-moz-placeholder, 
textarea:-moz-placeholder{ /* Mozilla Firefox 4 to 18 */
   color:    var(--heading-color);
   opacity:  1;
}
input::-moz-placeholder, 
textarea::-moz-placeholder{ /* Mozilla Firefox 19+ */
   color:    var(--heading-color);
   opacity:  1;
}
input:-ms-input-placeholder, 
textarea:-ms-input-placeholder{ /* Internet Explorer 10-11 */
   color:    var(--heading-color);
   opacity: 1;
}
input::-ms-input-placeholder, 
textarea::-ms-input-placeholder{ /* Microsoft Edge */
   color:    var(--heading-color);
   opacity: 1;
}

input::placeholder, 
textarea::placeholder{ /* Most modern browsers support this now. */
   color:    var(--heading-color);
   opacity: 1;
}


[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
  appearance: none;
}



