.zion-p{
    &-wraper{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 80px;
        @include max (1399px){
            gap: 60px;
        }
        @include max (1199px){
            gap: 40px;
        }
        @include max (991px){
            gap: 30px;
        }
         @include max (767px){
            display: block;
        }
    }
    &-wrap{
        margin-bottom: 80px;
        transition: all 0.4s;
        position: relative;
        @include max (1399px){
            margin-bottom: 60px;
        }
        @include max (1199px){
            margin-bottom: 40px;
        }
         @include max (991px){
            margin-bottom: 30px;
        }
        &:hover{
            .zion-p-thumbb img{
                transform: scale(1.1);
            }
            .zion-p-meta::before{
                width: 100%;
            }
            .zion-portfolio-btn{
                background-color: var(--accent-bg);
                border: 1px solid transparent;
                i{
                    transform: translate(42px, -42px);
                    color: var(--heading-color)!important;
                }
                &::before{
                    transform: translate(-24px,18px);
                }
            }
         
        }
    }
    &-thumb{
        visibility: hidden;
        position: relative;
        overflow: hidden;
        border-radius: 10px;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 10px;
            transform-origin: left;
        }
    }
    &-meta{
        padding-top: 24px;
        padding-bottom: 24px;
        position: relative!important;
        border-bottom: 1px solid rgba(29, 29, 27, 0.1);
        margin-bottom: 24px;
        &::before{
            content: " ";
            left: 0;
            bottom: 0;
            width: 0;
            height: 1px;
            position: absolute;
            transition: all .4s;
            background: var(--heading-color);
        }
        ul{
            li{
                display: inline-block;
                padding-right: 24px;
                position: relative;
                &::before{
                    content: "";
                    position: absolute;
                    width: 7px;
                    height: 7px;
                    top: 10px;
                    right: 5px;
                    transition: all .4s;
                    border-radius: 100px;
                    background-color: var(--heading-bg);
                }
                &:last-child::before{
                    content: none;
                }
                &:last-child{
                    padding-right: 0;
                }
                a{
                    font-size: 18px;
                    line-height: 1.44em;
                    color: var(--heading-color);
                }
            }
        }
    }
    &-content-wrap{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &-btn{
        text-align: center;
        margin-top: 80px;
        @include max (991px){
            margin-top: 60px;
        }
        @include max (767px){
            margin-top: 40px;
        }
    }
}

