.zion-faq{
  &-item{
    margin-bottom: 24px;
    background-color: var(--light-color2);
    padding: 30px;
    border-radius: 20px;
    position: sticky;
    transition: all 0.4;
    &.open {
      .zion-faq-body{
          display: block;
        }
        .zion-active-icon{
          transform: rotate(180deg);
        }
    }
  }
  &-header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
  }
  &-body{
    display: none;
    padding-right: 45px;
    padding-top: 10px;
  }
}


.zion-active-icon{
  flex-shrink: 0;
  transition: all 0.4s;
  img{
    @include max (767px){
      width: 40px;
    }
  }
}

