.breadcrumb{
  &-wrapper{
    text-align: center;
    padding: 200px 0 130px;
    position: relative;
    transition: all 0.4s;
    @include max(1199px) {
      padding: 180px 0 100px;
    }
    @include max(767px) {
      padding: 120px 0 80px;

    }
  }
  &-content {
    position: relative;
    max-width: 930px;
    text-align: center;
    margin: 0 auto;
    p{
      font-size: 21px;
    }
  }

  &-menu{
    ul{
      li{
        font-size: 21px;
        line-height: 30px;
        font-weight: 700;
        color: var(--accent-color);
        display: inline-block;
        padding: 0 6px;
        a{
          color: var(--heading-color);
        }
      }
    }
    &-wrap{
      padding-top: 50px;
      @include max (1199px){
        padding-top: 30px;
      }
    }
  }
}




.zion-errors{
  &-section{
    padding: 330px 0 130px;
    @include max(991px) {
      padding: 250px 0 100px;
    }
    @include max(767px) {
      padding: 170px 0 80px;
    }
  }
  &-content{
    max-width: 650px;
    margin: 0 auto;
    text-align: center;
    img{
      margin-bottom: 40px;
    }
    h2{
      margin-bottom: 15px;
    }
  }
}