.zion-team{
    &-wrap{
        margin-bottom: 24px;
        transition: all 0.4s;
        border-radius: 10px;
        background-color: var(--white-bg);
        &:hover{
            .zion-team{
                &-thumb img{
                    transform: scale(1.1);
                }
            }
        }
    }
    &-thumb{
        position: relative;
        overflow: hidden;
        border-radius: 10px;
        img{
            width: 100%;
            height: 100%;
            transition: all 0.4s;
            object-fit: cover;
            border-radius: 10px;
        }
    }
   
    &-content{
        padding: 30px;
        h4{
            padding-bottom: 10px;
        }
        p{
            color: var(--heading-color);
        }
    } 
   
   
}
