.zion-footer{
  &-section{
    padding-top: 130px;
    @include max (991px){
      padding-top: 100px;
    }
    @include max (767px){
      padding-top: 80px;
    }
  }
  &-textarea{
    @include max (1199px){
      max-width: 60%;
    }
  }
  &-menu-wraper{
    display: flex;
    float: inline-end;
    gap: 80px;
    @include max (1199px){
      float: inline-start;
      gap: 100px;
      padding-top: 80px;
    }
    @include max (991px){
      padding-top: 40px;
    }
    @include max (575px){
      padding-top: 30px;
      display: block;
    }
  }
  &-menu{
    h4{
      padding-bottom: 24px;
    }
    ul{
      li{
        padding-bottom: 10px;
        &:last-child{
          padding-bottom: 0;
        }
        a{
          color: var(--heading-color);
           display: inline-block;
          position: relative;
          text-transform: capitalize;
          &::before{
            content: "";
            position: absolute;
            bottom: -3px;
            left: auto;
            right: 0;
            height: 2px;
            width: 0;
            background-color: var(--heading-color) !important;
            transition: all .3s ease;
          }
          &:hover::before{
            left: 0;
            right: auto;
            width: 100%;
            @include max (991px){
              display: none;
            }
          }
        }
      }
    }
    &.menu2{
      @include max (575px){
        padding-bottom: 30px;
      }
    }
  }
  &-menu-wrap{
    display: flex;
    gap: 50px;
  }
  &-column{
    ul{
      li{
        padding-bottom: 10px;
        &:last-child{
          padding-bottom: 0;
        }
        a{
          color: var(--heading-color);
          display: inline-block;
          position: relative;
          text-transform: capitalize;
          &::before{
            content: "";
            position: absolute;
            bottom: -3px;
            left: auto;
            right: 0;
            height: 2px;
            width: 0;
            background-color: var(--heading-color) !important;
            transition: all .3s ease;
          }
          &:hover::before{
            left: 0;
            right: auto;
            width: 100%;
            @include max (991px){
              display: none;
            }
          }
        }
      }
    }
  }
  &-bottom-wrap{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 35px 0;
    margin-top: 80px;
    border-top: 1px solid rgba(29, 29, 27, 0.1);
    @include max (991px){
      margin-top: 60px;
    }
    @include max (767px){
      padding: 25px 0;
      margin-top: 40px;
    }
  }
}