.zion-service-content{
    &-wrap{
        @include max (991px){
            padding-top: 40px;
        }
        @include max (767px){
            padding-top: 30px;
        }
    }
    &-item {
        transition: all 0.4s;
        padding-bottom: 24px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.1);
        margin-bottom: 24px;
        position: relative;
        &.item2{
            padding-bottom: 0;
            border-bottom: none;
            margin-bottom: 0;
        }
        &.item3{
            padding: 24px;
            border-radius: 10px;
            margin: 0;
            &:hover{
                background-color: var(--heading-bg);
            }
        }
        &:hover{
            .zion-service-content-title h3{
                color: var(--accent-color);
            }
            .zion-service-content-icon {
                 span {
                    .arry1 {
                        top: -15px;
                        left: 15px;
                    }
                    .arry2 {
                        bottom: 0;
                        left: 0;
                    }
                }
            }

            .zion-service-content-thumb img {
                transform: scale(1);
            }
        }
    }
    &-wraper{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &-title{
        h3{
            color: var(--white-color);
            transition: all 0.4s;
        }
        &.title2{
            h3{
                color: var(--heading-color);
            }
        }
    }
    &-thumb{
        position: absolute;
        top: 0;
        right: 20%;
        z-index: 1;
        img{
            transition: all 0.4s;
            border-radius: 10px;
            transform: scale(0);
        }
    }
}

.light-color{
    color: var(--light-color1);
}