.zion-pagination {
  margin-top: 50px;
  @include max(991px) {
    margin-top: 30px;
  }
  ul{
    display: flex;
  }
  li {
    display: inline-block;
    margin: 0 2px;
  }
  &.center{
    ul{
      justify-content: center;
    }
  }
}
.zion-pagination{
  display: flex;
  &.center{
    justify-content: center;
  }
}
.zion-pagination span, 
.zion-pagination a,
.pagi-btn {
  height: 50px;
  width: 50px;
  font-weight: 600;
  display: inline-flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  margin: 0 5px;
  color: var(--heading-color);
  background: var(--light-color);
  border-radius: 100px;
  transition: all 0.4s;
  border: 1px solid var(--light-color);

  &:hover{
    color: var(--white-color);
    background-color: var(--heading-color);
    border: 1px solid var(--heading-color);
    path{
      stroke: var(--white-color);
    }
  }
  &.btn2{
    transform: rotate(180deg);
  }
}



a.current{
  cursor: not-allowed;
  color: var(--white-color);
  background-color: var(--heading-color);
  border: 1px solid var(--heading-color);
}

// End pagination